<template>
  <AppBorder :app-info=Login :size=size>
    <template v-slot:header>
      <div class="fx-ac">
        <span>üë®‚Äç‚öñÔ∏èÁî®Êà∑ÁôªÂΩï</span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver loginBox">
        <div class="bak">
        </div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="30px"
                 class="demo-ruleForm fx-ver fx-pac">
          <el-form-item label="üìî" prop="account">
            <el-input type="text" v-model="ruleForm.account" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üîí" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item class="">
            <el-button type="primary" @click="submitForm('ruleForm')">ÁôªÂΩï</el-button>
            <el-button @click="resetForm('ruleForm')">ÈáçÁΩÆ</el-button>
          </el-form-item>
        </el-form>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'

export default {
  name: "Login.vue",
  components: {AppBorder},
  data() {
    const validateAccount = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('ËØ∑ËæìÂÖ•Ë¥¶Âè∑'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('ËØ∑ËæìÂÖ•ÂØÜÁ†Å'));
      } else {
        callback();
      }
    };
    return {
      name: "Login",
      Login: {
        name: 'Login',
        isLoad: false,
        min: true,
        shortcut: 'fa-sign-in'
      },
      ruleForm: {
        account: '',
        checkPass: '',
      },
      rules: {
        account: [
          {validator: validateAccount, trigger: 'blur'}
        ],
        checkPass: [
          {validator: validatePass, trigger: 'blur'}
        ],
      },
      size: {
        width: '435px',
        height: '315px'
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.postBody('/api/login', {
            username: this.ruleForm.account,
            password: this.ruleForm.checkPass
          }).then(data => {
            if (data.code === 200) {
              let token = data.msg;
              localStorage.setItem('token', token);
              this.$store.commit("setToken", token);
              this.$message.success("ÁôªÂΩïÊàêÂäüÔºÅ");
              this.$router.push("/desktop");
            } else
              this.$message.error(data.msg)
          }).catch(err => {
            this.$message.error(err);
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    closeApp(){
      this.$router.push("/desktop");
    }
  }
}
</script>

<style scoped>
.loginBox {
  background-color: white;
  width: 433px;
  height: 99%;
  border: 1px saddlebrown solid;
}

.bak {
  width: 100%;
  height: 70px;
  background-color: #73b9fd;
}
</style>