class PageManager {
  constructor() {
    this.minappMap = {
      minApp: []
    };
    this.appMap = {
      'ChatRoom': {
        name: 'ChatRoom',
        isLoad: false,
        min: true,
        icon: 'üí¨',
        CName: 'ÈíàÈíà'
      },
      'Brower': {
        name: 'Brower',
        isLoad: false,
        min: true,
        icon: 'üåê',
        CName: 'ÊµèËßàÂô®'
      },
      'Live2DShow': {
        name: 'Live2DShow',
        isLoad: false,
        min: true,
        icon: 'üëØ‚Äç‚ôÄÔ∏è',
        CName: 'Live2D'
      },
      'Lyre': {
        name: 'Lyre',
        isLoad: false,
        min: true,
        icon: 'üéπ',
        CName: 'È£éÁâ©‰πãËØóÁê¥'
      },
      'Clipboard': {
        name: 'Clipboard',
        isLoad: false,
        min: true,
        icon: 'üá®üáµ',
        CName: '‰∏ÄÈîÆÂ§çÂà∂'
      },
      'Video': {
        name: 'Video',
        isLoad: false,
        min: true,
        icon: 'üì∫',
        CName: 'FlvÊí≠ÊîæÂô®'
      },
    };
    this.focusApp = {focus: null, name: null};
    window.focusApp = this.focusApp;
  }

  getMinAppMap() {
    return this.minappMap;
  }

  getAppInfo(name) {
    return this.appMap[name];
  }

  minimize(appName) {
    if (this.appMap[appName]) {
      this.minappMap.minApp.push(this.appMap[appName]);
      this.appMap[appName].min = false;
    }
  }

  getAppMap() {
    return this.appMap;
  }

  restore(minApp) {
    const idx = this.minappMap.minApp.indexOf(minApp);
    if (idx >= 0) {
      this.minappMap.minApp.splice(idx, 1);
      minApp.min = true
    }
  }

  closeApp(name) {
    if (this.appMap[name]) {
      this.appMap[name].isLoad = false;
    }
  }

  getTranslateParams(ev, srcMouseX, srcMouseY, curOffsetX, curOffsetY, curScale, width, height) {
    return [
      ev.clientX - srcMouseX + curOffsetX,
      ev.clientY - srcMouseY + curOffsetY
    ]
  }

  setFocusApp(that) {
    this.focusApp.focus = that;
    this.focusApp.name = that.AppInfo.name;
  }

  getFocusApp() {
    return this.focusApp;
  }

  mousemove(ev) {
    ev.preventDefault();
    const that = this.focusApp.focus;
    if (that && that.mouseDown) {
      if (that.positionOnclick[6] !== 'default') {
        const dx = ev.clientX - that.positionOnclick[2];
        const dy = ev.clientY - that.positionOnclick[3];
        if (that.positionOnclick[6] !== 'ns-resize') {
          that.size.width = that.positionOnclick[4] + dx * that.direction[0] + 'px';
          if (that.direction[0] === -1) {
            console.log(that.positionOnclick[7], dx, 'px');
            that.position.left = that.positionOnclick[7] + dx + 'px';
          }
        }
        if (that.positionOnclick[6] !== 'ew-resize') {
          that.size.height = that.positionOnclick[5] + dy * that.direction[1] + 'px';
          if (that.direction[1] === -1) that.position.top = that.positionOnclick[8] + dy + 'px';
        }
        return;
      }
      if (that.isMove) {
        const params = this.getTranslateParams(ev, that.positionOnclick[2], that.positionOnclick[3],
          that.positionOnclick[0], that.positionOnclick[1], that.transform[0], window.innerWidth, window.innerHeight);
        that.transform[4] = params[0];
        that.transform[5] = params[1];
        that.transform = [...that.transform];
      }
    }
  }

  mouseup(ev) {
    const that = this.focusApp.focus;
    that && that.mouseup && that.mouseup(ev);
  }

  keydown(ev) {
    const that = this.focusApp.focus;
    that && that.onKeyDown(ev);
  }
}

const pageManager = new PageManager();
export default pageManager;
