<template>
  <div :id="AppInfo.name"
       :class="`${focusApp.name === AppInfo.name ?'focus':'normal'} fx-ver`"
       :style="{
        'position': 'absolute',
        'transform': this.tf,
        'width': this.size.width || 'auto',
        'height': this.size.height || 'auto',
        'cursor': this.cursor,
        'top': this.position.top || 'auto',
        'left': this.position.left || 'auto'
      }"
       v-show="AppInfo.min" @mousemove="mousemove" @mousedown="mousedown">
    <div :class="`${herderCss||'header'} fx-ac fx-pj`">
      <slot name="header"></slot>
      <div class="fx-ac">
        <span class="fa fa-minus-square fz18 middleButton" @click="minimize"></span>
        <span class="fa fa-square fz18 middleButton" @click="maximize"></span>
        <span class="fa fa-times fz18 middleButton" @click="closeApp"></span>
      </div>
    </div>
    <slot name="main"></slot>
  </div>
</template>

<script>
import pageManager from "@/utils/PageManager";

export default {
  name: "AppBorder",
  props: {
    AppInfo: {type: Object, required: true}, herderCss: [String],
    size: {
      type: Object,
      default: () => {
        return {};
      },
    },
    position: {
      type: Object,
      default: () => {
        return {};
      },
    }
  },
  computed: {
    tf() {
      return "matrix(" + this.transform.toString() + ")";
    },
  },
  mounted() {
    this.mainBox = document.getElementById(this.AppInfo.name);
    let DOMRect = this.mainBox.getBoundingClientRect();
    this.position.left = DOMRect.left + 'px';
    this.position.top = DOMRect.top + 'px';
    pageManager.setFocusApp(this);
  },
  data() {
    return {
      focusApp: pageManager.getFocusApp(),
      mouseDown: false,
      isMove: false,
      positionOnclick: [],
      transform: [1, 0, 0, 1, 0, 0],
      sizeChange: {
        isMax: false,
        width: null,
        height: null,
        top: null,
        left: null
      },
      direction: [1, 1],
      cursor: 'default',
      mainBox: null,
    }
  },
  methods: {
    onKeyDown(ev) {
      this.$parent.onKeyDown && this.$parent.onKeyDown(ev);
    },
    mousedown(ev) {
      let DOMRect = this.mainBox.getBoundingClientRect();
      let dx = ev.clientX - DOMRect.left;
      let dy = ev.clientY - DOMRect.top;
      pageManager.setFocusApp(this);
      this.positionOnclick[2] = ev.clientX;
      this.positionOnclick[3] = ev.clientY;
      this.positionOnclick[6] = this.cursor;
      if (dy < 40) {
        this.positionOnclick[0] = this.transform[4];
        this.positionOnclick[1] = this.transform[5];
        this.isMove = true;
      }
      if (dx < DOMRect.width / 2) this.direction[0] = -1;
      else this.direction[0] = 1;
      if (dy < DOMRect.height / 2) this.direction[1] = -1;
      else this.direction[1] = 1;

      this.positionOnclick[4] = DOMRect.width;
      this.positionOnclick[5] = DOMRect.height;
      this.positionOnclick[7] = Number(this.position.left.substring(0, this.position.left.length - 2));
      this.positionOnclick[8] = Number(this.position.top.substring(0, this.position.top.length - 2));
      this.mouseDown = true;
    },
    mouseup() {
      this.mouseDown && (this.positionOnclick[6] !== 'default') && this.$parent.onResize && this.$parent.onResize();
      this.mouseDown = false;
      this.isMove = false;
      this.positionOnclick[6] = 'default';
    },
    mousemove(ev) {
      let DOMRect = this.mainBox.getBoundingClientRect();
      let dx = ev.clientX - DOMRect.left;
      let dy = ev.clientY - DOMRect.top;
      let right = DOMRect.width - dx;
      let bottom = DOMRect.height - dy;
      if ((dx < 3 && dy < 3) || (bottom < 3 && right < 3)) {
        this.cursor = 'nwse-resize'
      } else if ((dy < 3 && right < 3) || (dx < 3 && bottom < 3)) {
        this.cursor = 'nesw-resize'
      } else if (dy < 3 || bottom < 3) {
        this.cursor = 'ns-resize'
      } else if (dx < 3 || right < 3) {
        this.cursor = 'ew-resize'
      } else {
        this.cursor = 'default'
      }
    },
    minimize() {
      pageManager.minimize(this.AppInfo.name);
    },
    maximize() {
      if (!this.sizeChange.isMax) {
        this.sizeChange.width = this.size.width;
        this.sizeChange.height = this.size.height;
        this.sizeChange.left = this.position.left;
        this.sizeChange.top = this.position.top;
        this.size.height = '100%';
        this.size.width = '100%';
        this.sizeChange.isMax = true;
        this.position.top = '0px';
        this.position.left = '0px';
        this.transform = [1, 0, 0, 1, 0, 0];
        this.$nextTick(() => {
          this.$parent.onResize && this.$parent.onResize()
        })
      } else {
        this.size.width = this.sizeChange.width;
        this.size.height = this.sizeChange.height;
        this.position.top = this.sizeChange.top;
        this.position.left = this.sizeChange.left;
        this.sizeChange.isMax = false
        this.$nextTick(() => {
          this.$parent.onResize && this.$parent.onResize()
        })
      }
    },
    closeApp() {
      this.$parent.closeApp && this.$parent.closeApp();
      pageManager.closeApp(this.AppInfo.name);
    },
  }
}

</script>

<style scoped>
.header1 {
  height: 40px;
  width: 100%;
  background-color: #48cbd4;
}
</style>